:root {
  font-size: 62.5;
  font-family: 'Roboto Condensed', sans-serif; }

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.header {
  height: 25vh;
  background-image: url("../images/mickey.jpeg");
  background-size: contain;
  display: flex;
  justify-content: space-between;
  align-items: end;
  padding: 0 20px 0 20px; }

.title {
  color: black;
  font-weight: bold;
  font-size: 2rem;
  background-color: #eeee7f;
  padding: 15px;
  border: 5px solid #e26161;
  position: relative;
  bottom: -20px; }

.form {
  background-color: #eeee7f;
  padding: 5px;
  border: 4px solid #e26161;
  position: relative;
  bottom: -20px; }

.main {
  display: flex;
  padding: 20px 0 60px;
  background-color: #f6eeec; }

.sectionFavorite {
  padding: 0px 20px;
  width: fit-content; }

.sectionMain {
  padding: 0 20px;
  width: fit-content; }

.sectionTitle {
  text-align: center;
  padding: 20px 0;
  font-weight: bold;
  padding-right: 5px; }

.favoriteTitleContainer {
  display: flex;
  justify-content: center;
  align-items: center; }

.listFavorite {
  display: flex;
  flex-direction: column;
  gap: 20px; }

.listMain {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 20px; }

.card {
  background-color: white;
  padding: 20px 20px 10px;
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 150px;
  cursor: pointer; }
  .card__favorite {
    background-color: #000000; }
  .card__imgContainer {
    height: 150px;
    display: flex;
    justify-content: center;
    overflow: hidden; }
    .card__imgContainer .characterImg {
      height: 150px; }
  .card__nameContainer {
    display: flex;
    gap: 5px; }
    .card__nameContainer .white {
      color: white; }
    .card__nameContainer .hidden {
      display: none; }

.footer {
  height: 20px;
  padding: 10px 20px;
  border-top: 2px solid #e26161;
  background-color: #eeee7f;
  position: fixed;
  bottom: 0;
  width: 100vw; }

.footerContent {
  display: flex;
  justify-content: center;
  align-items: center; }
  .footerContent__text {
    color: #413e3e;
    font-size: 0.9rem; }
  .footerContent__icon {
    margin: 0 3px; }
